html,body {
  height:100%;
}

%wrap {
  width: 980px;
  margin: auto;
  @extend %clearfix;
}

body {
  font-size: 12px;
  color: #888;
}

a {
  color: #666;
  &:hover {
    color: #ff0000;
  }
}

ul,li {
  margin: 0;
  padding: 0;
  border: none;
}

li {
  list-style: none;
}

h3,h4 {
  font-size: 12px;
  font-weight: normal;
  margin: 0;
  padding: 0;
}

form {
  margin:0;
}


#header {
  height: 44px;
  line-height: 44px;
  color: #fff;
  background-color: #ff9695;

  h1 {
    font-size: 18px;
    margin: 0;
  }

  a {
    color: #fff;
  }

  .am-text-left {
    .logo {
      display: block;
      text-align: center;
      white-space: nowrap;
      font-weight: bold;
      font-size: 20px;
    }
  }

  .am-icon-md {
    color: #fff;
  }

  .am-btn-link {
    color: #fff;
    text-decoration: none;
  }

  .col-title {
    position: relative;
    white-space: nowrap;
    overflow: hidden;
  }

  .icon-link {
    width: 40px;
    display: inline-block;
    text-align: right;

    i {
      vertical-align: -10%;
    }
  }

  .col-right {
    .icon-link {
      text-align: right;
    }
  }

  .search {
    height: 30px;
    border-radius: 30px;
    border: none;
    display: block;
    width: 100%;
    margin-top: 7px;
    font-size: 1.3rem;
    padding: 0 0 0 40px;
    background: #ff7b79;
    color: #fff;
    &::placeholder {
      color: #ffd0d8;
    }
  }

  .submit {
    display: block;
    background: url('../images/common/icon-search.png') no-repeat center;
    background-size: contain;
    position: absolute;
    width: 30px;
    height: 30px;
    left: 15px;
    top: 7px;
    border: none;
    background-color: transparent;
    text-indent: -9999px;
  }

  .btn-cart, .btn-more {
    width: 30px;
    height: 30px;
    display: block;
    margin: 7px auto 0;
    position: relative;
    span {
      width: 16px;
      height: 16px;
      line-height: 16px;
      text-align: center;
      background: #fff;
      color: #fff;
      border-radius: 16px;
      position: absolute;
      top: 0;
      right: -4px;

    }
  }
}


#nav {
  display: flex;
  height: 45px;
  line-height: 45px;
  a {
    display: block;
    flex: 1;
    text-align: center;
    font-size: 1.5rem;

    &.current {
      color: #ff8887;
      font-weight: bold;
      span {
        border-bottom: 2px solid #ff8887;
        padding: 4px;
      }
    }
  }
}

#slider {
  height: 210px;
  overflow: hidden;
  .am-control-nav li a {
    border-radius: 0;
    width: 20px;
    height: 5px;
    background: #fff;
    box-shadow:none;
    &.am-active {
      background: #ff8887;
    }
  }
}